body{
  margin:auto;
  max-width: 600px;
  background-color: #f5f5f5;
}
button {
  padding: 10px;
  margin: 5px;
  margin-right: 15px;
  width: 170px;
  color: white;
  font-weight: bold;
  font-size: 17px;
  background-color:#4a20e4 ;
  border:none;
}
button:hover{
  background-color: blue;
}
td{
  border-bottom: 1px solid #ddd;
  padding: 10px;
  margin: 0 auto;
}
table{
  margin:0 auto;
  width: 80%;
}
fieldset{
  padding: 10px;
  border-color: #ffd902 ;
  margin-top: 0.5cm;
  margin-bottom: 0.5cm;
  margin-right: 15px;
  margin-left: 15px;
}
legend{
color: #0E0042;
}
p{
  text-align: center;
}
img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 200px;
  width: 200px;
  margin-top: 0.5cm;
  margin-bottom: 1cm;
}
.modal{
  padding:10px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
  
  
}
input{
  width:95%;
  height: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  font-family:'Lucida Grande';
  font-size: 18px;
}
label{
  font-weight: bold;
  font-family:Tahoma;
}
.buttons{
  display: inline-flex;
}
.close{
  background-color: grey;
  width:100px;
}
.bill{
  width:100px ;
}
h2{
  color: red;
  margin-left: 37%;
  margin-bottom: 0px;
  font-weight:17px;
  font-family:'Lucida Sans';
}
.pow{
  width: 400px;
  margin-top: 0px;
  height: 200px;
}

.functionalities{
 display:block;
}

@media only screen and (max-width: 600px) {
  button {
    width:95%;
   
  }
  .functionalities{
    margin:0 auto;
    text-align: center;
    padding: 0px;
  }
  table{
    width: 100%;
    padding:10px
  }
}
